.denhaag-card-group {
  --denhaag-card-group-distanced: 0;
  display: grid;
  gap: var(--_denhaag-card-group-responsive-gap, var(--denhaag-card-group-gap));
  margin-block-start: calc(var(--denhaag-card-group-distanced) * var(--denhaag-card-group-margin-block));
  margin-block-end: calc(var(--denhaag-card-group-distanced) * var(--denhaag-card-group-margin-block));
  margin-inline-start: calc(var(--denhaag-card-group-distanced) * var(--denhaag-card-group-margin-inline));
  margin-inline-end: calc(var(--denhaag-card-group-distanced) * var(--denhaag-card-group-margin-inline));
}
@media (width >= 768px) {
  .denhaag-card-group {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (width >= 1024px) {
  .denhaag-card-group {
    grid-template-columns: repeat(3, 1fr);
  }
}

.denhaag-card-group--rows .denhaag-card-authentication {
  display: grid;
  grid-template-rows: auto auto 1fr;
  min-block-size: var(--denhaag-card-authentication-portrait-min-block-size);
  text-align: center;
}
.denhaag-card-group--rows .denhaag-card-authentication .denhaag-card-authentication__header {
  grid-template-areas: "card-logo" "card-heading";
  grid-template-rows: calc(var(--denhaag-card-authentication-portrait-logo-size) + var(--denhaag-card-authentication-portrait-logo-margin-block-end)) auto;
  margin-block-end: var(--denhaag-card-authentication-portrait-header-margin-block-end);
}
.denhaag-card-group--rows .denhaag-card-authentication .denhaag-card-authentication__logo {
  height: var(--denhaag-card-authentication-portrait-logo-size);
  width: var(--denhaag-card-authentication-portrait-logo-size);
  margin-inline-start: auto;
  margin-inline-end: auto;
  margin-block-end: var(--denhaag-card-authentication-portrait-logo-margin-block-end);
}
.denhaag-card-group--rows .denhaag-card-authentication .denhaag-card-authentication__title {
  margin-block-end: var(--denhaag-card-authentication-title-margin-block-end);
}
.denhaag-card-group--rows .denhaag-card-authentication .denhaag-card-authentication__cta {
  display: flex;
  justify-content: center;
}
.denhaag-card-group--rows .denhaag-card-authentication .denhaag-card-authentication__footer {
  display: flex;
  flex-direction: column;
  justify-content: end;
  margin-block-start: var(--denhaag-card-authentication-portrait-footer-margin-block-start);
}

.denhaag-card-group--distanced {
  --denhaag-card-group-distanced: 1;
}

.denhaag-card-group--responsive {
  --_denhaag-card-group-responsive-gap: var(--denhaag-card-group-xs-gap, var(--denhaag-card-group-gap));
  --denhaag-card-authentication-portrait-max-inline-size: calc((100% / 3) - var(--_denhaag-card-group-responsive-gap));
}
@media (width >= 768px) {
  .denhaag-card-group--responsive {
    --_denhaag-card-group-responsive-gap: var(--denhaag-card-group-s-gap, var(--denhaag-card-group-gap));
  }
  .denhaag-card-group--responsive .denhaag-card-authentication {
    display: grid;
    grid-template-rows: auto auto 1fr;
    min-block-size: var(--denhaag-card-authentication-portrait-min-block-size);
    text-align: center;
  }
  .denhaag-card-group--responsive .denhaag-card-authentication .denhaag-card-authentication__header {
    grid-template-areas: "card-logo" "card-heading";
    grid-template-rows: calc(var(--denhaag-card-authentication-portrait-logo-size) + var(--denhaag-card-authentication-portrait-logo-margin-block-end)) auto;
    margin-block-end: var(--denhaag-card-authentication-portrait-header-margin-block-end);
  }
  .denhaag-card-group--responsive .denhaag-card-authentication .denhaag-card-authentication__logo {
    height: var(--denhaag-card-authentication-portrait-logo-size);
    width: var(--denhaag-card-authentication-portrait-logo-size);
    margin-inline-start: auto;
    margin-inline-end: auto;
    margin-block-end: var(--denhaag-card-authentication-portrait-logo-margin-block-end);
  }
  .denhaag-card-group--responsive .denhaag-card-authentication .denhaag-card-authentication__title {
    margin-block-end: var(--denhaag-card-authentication-title-margin-block-end);
  }
  .denhaag-card-group--responsive .denhaag-card-authentication .denhaag-card-authentication__cta {
    display: flex;
    justify-content: center;
  }
  .denhaag-card-group--responsive .denhaag-card-authentication .denhaag-card-authentication__footer {
    display: flex;
    flex-direction: column;
    justify-content: end;
    margin-block-start: var(--denhaag-card-authentication-portrait-footer-margin-block-start);
  }
}
@media (width >= 1024px) {
  .denhaag-card-group--responsive {
    --_denhaag-card-group-responsive-gap: var(--denhaag-card-group-m-gap, var(--denhaag-card-group-gap));
  }
}
@media (width >= 1280px) {
  .denhaag-card-group--responsive {
    --_denhaag-card-group-responsive-gap: var(--denhaag-card-group-l-gap, var(--denhaag-card-group-gap));
  }
}
@media (width >= 1768px) {
  .denhaag-card-group--responsive {
    --_denhaag-card-group-responsive-gap: var(--denhaag-card-group-xl-gap, var(--denhaag-card-group-gap));
  }
}